import React, { useState } from 'react'

import { Form, FormGroup, Input, Label } from 'reactstrap';


export default function Editor(args) {
  var [formData, setFormData] = useState({
    artistId    : args.artistId ? args.artistId : '',
    artistName  : args.artistName ? args.artistName : '',
    albumName   : args.albumName ? args.albumName : '',
    imageUrl    : args.imageUrl ? args.imageUrl : '',
    sampleUrl   : args.sampleUrl ? args.sampleUrl : '',
    releaseDate : args.releaseDate ? args.releaseDate : '',
    price       : args.price ? args.price : 0
  })

  var modifyData = (e) => {
    var t = {...formData};
    t[e.target.name] = e.target.value
    setFormData(t)
    args.onChange(t)
  }
    
  return (
    <Form className='w-100'>
        <div className="container">
            <div className="row">
                <div className="col-md-12">
                    <FormGroup>
                        <Label for="albumName">
                        Album Name
                        </Label>
                        <Input
                        id="albumName"
                        name="albumName" readOnly={args.readOnly}
                        placeholder="Enter album name"
                        value={formData.albumName} 
                        onChange={modifyData} 
                        />
                    </FormGroup>
                </div>
            </div>
            <div className="row">
                <div className="col-md-12">
                    <FormGroup>
                    <Label for="artistName">
                        Artist Name
                    </Label>
                    <Input
                        id="artistName"
                        name="artistName"
                        placeholder="Enter artist name"  readOnly={args.readOnly}
                        value={formData.artistName} 
                        onChange={modifyData}
                    />
                    </FormGroup>
                </div>
            </div>

            <div className="row">
                <div className="col-md-6">
                    <FormGroup>
                        <Label for="releaseDate">
                            Release Date
                        </Label>
                        <Input
                            id="releaseDate"
                            name="releaseDate"
                            placeholder="Please Enter Release Date"  readOnly={args.readOnly}
                            value={formData.releaseDate.substring(0,10)}
                            onChange={modifyData}
                            type="date"
                        />
                    </FormGroup>
                    
                </div>
                <div className="col-md-6">
                    <FormGroup>
                        <Label for="price">
                            Price
                        </Label>
                        <Input
                            id="price"
                            name="price"
                            placeholder="Enter Price Date"  readOnly={args.readOnly}
                            value={formData.price}
                            onChange={modifyData}
                            type="number"
                        />
                    </FormGroup>
                    
                </div>
            </div>

            <div className="row">
                <div className="col-md-12">
                    <FormGroup>
                        <Label for="sampleUrl">
                            Sample Audio Url
                        </Label>
                        <Input
                            id="sampleUrl"
                            name="sampleUrl"
                            placeholder="Enter Sample Audio Url"  readOnly={args.readOnly}
                            value={formData.sampleUrl}
                            onChange={modifyData}
                        />
                    </FormGroup>
                </div>
            </div>

            <div className="row">
                <div className="col-md-8">
                    <FormGroup>
                        <Label for="imageUrl">
                            Album Cover
                        </Label>
                        <Input
                            id="imageUrl"
                            name="imageUrl"
                            placeholder="Enter Album Cover Url"  readOnly={args.readOnly}
                            value={formData.imageUrl}
                            onChange={modifyData}
                        />
                    </FormGroup>
                </div>
                <div className="col-md-4">
                    <img src={formData.imageUrl} className="shadow border-0 w-100"/>
                </div>
            </div>

        </div>
        
        
       
        
</Form>
  )
}
