import React, {useState} from 'react'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Progress } from 'reactstrap';
import Editor from './editor';
var player = new Audio();

export default function Datagrid(args) {
  var [selected, setSelected] = useState({});
  const [modalEdit, setModalEdit] = useState(false);
  const [modalDelete, setModalDelete] = useState(false);

  const toggleEdit = () => setModalEdit(!modalEdit);
  const toggleDelete = () => setModalDelete(!modalDelete);

  var [progress, setProgress] = useState(0);
  var [durasi, setDurasi] = useState(0);
  var [volume, setVolume] = useState(0);
  var [isPlaying, setIsPlaying] = useState(false);
  const [modalPlay, setmodalPlay] = useState(false);
  const toggleModalPlay = () => {
        setmodalPlay(!modalPlay); 
        if(!modalPlay)
        {
            player.pause();
            clearInterval(player.bx);
            setIsPlaying(false);
            setProgress(0)
        }
        
    };

    const onClick =(e) =>{
        setSelected(e);
        console.log(e);
    }

    const onPlayDialogClick = (e) => {
        setSelected(e);
        setmodalPlay(true);
    }

    const onEditDialogClick = (e) => {
        // setSelected(e);
        setModalEdit(true);
    }

    const onDeleteDialogClick = (e) => {
        // setSelected(e);
        setModalDelete(true);
    }

    const onChange = (e) => {
        setSelected(e);
      }

    const onUpdate = (e) => {
        if (args.onUpdate(selected))
            setModalEdit(!modalEdit)
      }

      const onDelete = () => {
        if (args.onDelete(selected))
            setModalDelete(!modalDelete)
      }    


  const drawGridItem = (item, indeks) => {
    return (
        <tr key={item.artistId} item={JSON.stringify(item)} onClick={()=> {onClick(item)}}>
            <td>{indeks+1}</td>
            <td>
                <div className="content">
                    <img className="cover" src={item.imageUrl} alt=""/>
                    <div>
                        <strong>{item.albumName}</strong>
                    </div>
                </div>
            </td>
            <td>
                <div className="content">
                    <div className="ptitle">Artist Name</div>
                    <div>
                    {item.artistName}
                    </div>
                </div>
            </td>
            <td>
                <div className="content">
                    <div className="ptitle">Release Date</div>
                    <div>
                        {`${item.releaseDate.substring(8,10)} - ${item.releaseDate.substring(5,7)} - ${item.releaseDate.substring(0,4)}`} 
                    </div>
                </div>
            </td>
            <td>
                <div className="content">
                    <div className="ptitle">Price</div>
                    <div>
                        USD {item.price}
                    </div>
                </div>
            </td>
            <td><button className='btn btn-info btn-sm' onClick={()=>{onPlayDialogClick(item)}}><i className="fa-solid fa-play"></i></button></td>
            <td>
                <div className="content">
                    <button className='mr-6 btn btn-success btn-sm' onClick={()=> {onEditDialogClick(item)}}>Edit</button> 
                    <button className='mr-6 btn btn-warning btn-sm' onClick={()=> {onDeleteDialogClick(item)}}>Delete</button>
                </div>
            </td>                
        </tr>
    )
  }

  return (
    <>
        <table style={{marginTop:"2px"}}>
                    <thead>
                        <tr>
                            <th>Num. </th>
                            <th>Album Name   </th>
                            <th>Artist Name  </th>
                            <th>Date Release </th>
                            <th>Price        </th>
                            <th>Sample Audio </th>
                            <th>Action       </th>
                        </tr>
                    </thead>
                    <tbody>
                        { (args.data.list.length>0) ? args.data.list.map((item, indeks)=> drawGridItem(item, indeks)) : <tr><td colSpan={7} style={{textAlign:'center',padding:'20px'}}><h5><i className="fa fa-music"></i> No Data Found</h5></td></tr>}
                    </tbody>
        </table>

        <Modal isOpen={modalPlay} toggle={toggleModalPlay} backdrop={true} size="md" {...args} contentClassName="border-2 shadow" onClosed={()=>{player.pause(); player.currentTime = 0; setProgress(0); setIsPlaying(false)}}>
            <ModalHeader toggle={toggleModalPlay} className="border-0 text-bg-secondary" close={<></>}>Media Collection Info</ModalHeader>
            <ModalBody className='text-bg-secondary'>
                {/* <Editor artistName="" albumName="" price={0.00} releaseDate="" imageUrl="" sampleUrl="" onChange={onChange}/> */}
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 mb-2">
                            
                            <div className='container'>
                                <div className="row">
                                    <div className="col-md-12">
                                        <div>Album Title</div>                            
                                        <div>{selected.albumName}</div>  
                                    </div>
                                </div>
                            </div>
                            <div className='container'>
                                <div className="row mt-5">
                                    <div className="col-md-6">
                                        <div>Artist</div>                            
                                        <div>{selected.artistName}</div>                            
                                    </div>
                                    <div className="col-md-6">
                                        <div>Release Date</div>                            
                                        <div>{selected.releaseDate}</div>                            
                                    </div>
                                </div>
                            </div>
                            
                        </div>
                        <div className="col-md-4">
                            <img className="shadow w-100 rounded" src={selected.imageUrl}/>
                        </div>
                    </div>
                </div>

            </ModalBody>
            <ModalFooter className='border-0 text-bg-secondary'>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="d-flex flex-row mb-3 justify-content-between">
                                <Button color="success" onClick={()=>{
                                    if (!isPlaying)
                                    {
                                        player.src = selected.sampleUrl;
                                        player.onloadedmetadata = ()=> {
                                            player.dp = player.duration;
                                            setDurasi(player.duration);
                                            player.volume = 1;
                                            setVolume(player.volume);

                                            window.px = player;
                                            
                                        }
                                        player.onplaying = () => {
                                            player.bx = setInterval(()=>{
                                                setVolume(player.volume);
                                                setProgress((player.currentTime  / player.duration) * 100);
                                            },100)
                                            
                                        }
                                        player.onended = () => {
                                            clearInterval(player.bx);
                                            //isPlaying = false;
                                            setIsPlaying(false);
                                            setProgress(0);
                                        }
    
                                        player.currentTime = 0;
                                        setProgress(0);
                                        player.play();
                                        //isPlaying = true;
                                        setIsPlaying(true);
                                    }
                                    else {
                                        window.px.pause();
                                        //isPlaying = false;
                                        setProgress(0);
                                        setIsPlaying(false);
                                        clearInterval(player.bx);
                                    }
                                }} className="btn-sm m-2" style={{width:'55px'}}>
                                    {isPlaying?<i className='fa fa-stop'></i>: <i className='fa fa-play'></i>}
                                </Button>
                                <div className="p-2" style={{width:"80%"}}>
                                    <Progress className="my-3" style={{height: '3px'}} value={progress}/>
                                    <div className="d-flex flex-row justify-content-between" style={{marginTop:'-5px'}}>
                                        <div>00:00</div>
                                        <div>{`${Math.floor(durasi/60).toString().padStart(2,0)}:${Math.floor(durasi%60).toString().padStart(2,0)}`}</div>
                                    </div>
                                </div>
                                <div className="p-2"  style={{width:"20%"}}>
                                    <Progress className="my-3" style={{height: '3px'}} value={volume*100}/>
                                    <div className="d-flex flex-row justify-content-between" style={{marginTop:'-12px'}}>
                                        <button className="" onClick={
                                            ()=> {
                                                if (window.px.volume > 0)
                                                    window.px.volume -= .1; 
                                                setVolume(window.px.volume)
                                            }
                                        }><i className="fa fa-minus"></i></button>
                                        <button className="" onClick={
                                            ()=> {
                                                if (window.px.volume < 1)
                                                    window.px.volume += .1;
                                                setVolume(window.px.volume)
                                            }
                                        }><i className="fa fa-plus"></i></button>

                                    </div>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                    
                    
                    <Button color="light" onClick={toggleModalPlay} className="btn-sm m-2">
                        Close
                    </Button>
                    
                </div>
            </ModalFooter>
        </Modal>


        <Modal isOpen={modalEdit} toggle={toggleEdit} backdrop={true} size="lg" {...args} contentClassName="border-2 shadow" >
        <ModalHeader toggle={toggleEdit} className="border-0 text-bg-success" close={<></>}>Edit Collection : #{selected.artistId}</ModalHeader>
        <ModalBody>
          <Editor {...selected}
             onChange={onChange}
          />
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={onUpdate}>Update</Button>
          <Button color="secondary" onClick={toggleEdit}>Cancel</Button>
        </ModalFooter>
      </Modal>

      <Modal isOpen={modalDelete} toggle={toggleDelete} backdrop={false} size="lg" {...args} contentClassName="border-2 shadow">
        <ModalHeader toggle={toggleDelete} className="border-0 text-bg-warning" close={<></>}>Delete Collection : #{selected.artistId}</ModalHeader>
        <ModalBody>
          <Editor {...selected}  readOnly={true} onChange={()=>{}}/>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={onDelete}>
            Delete
          </Button>{' '}
          <Button color="secondary" onClick={toggleDelete}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>

      
    </>
  )
}
