import React, {useState} from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Progress } from 'reactstrap';
import Editor from './editor';

export default function Header(args) {
  const buttonStyle = {height: '33px',marginTop : '-1px'};
  var [searchText, setSearchText] = useState('');
  var [formData, setFormData] = useState({
    artistId    : '',
    artistName  : '',
    albumName   : '',
    imageUrl    : '',
    sampleUrl   : '',
    releaseDate : '',
    price       : 0
  })
  

  const [modalAddNew, setModalAddNew] = useState(false);
  const toggleAddNew = () => setModalAddNew(!modalAddNew);

  

  // On Search Input Change Do Something
  const onSearchChange = (e) => {
    setSearchText(e.target.value);
    args.onSearch(e.target.value);
  }

  const onChange = (e) => {
    setFormData(e)
  }

  const onSimpan = async () => {
    if (args.onSave(formData))
    {
      setModalAddNew(!modalAddNew)
      setFormData({
        artistId    : '',
        artistName  : '',
        albumName   : '',
        imageUrl    : '',
        sampleUrl   : '',
        releaseDate : '',
        price       : 0
      })
    }
  }



  return (
    <>
        <header className="navbar navbar-expand-lg navbar-dark bg-dark sticky-top flex-md-nowrap p-2">
        <div className="navbar-brand col-md-3 col-lg-2 me-0 px-3 fs-6 page-title">Media Collections</div>
            <input 
                className="form-control form-control-dark w-100 rounded-2 border-0" 
                type="text" 
                placeholder="Search" 
                aria-label="Search" 
                value={searchText}
                onChange={onSearchChange}
                style={{padding:'.4rem'}}
            />
            <div className="navbar-nav btn-add">
                <div className="nav-item text-nowrap"><button className="btn btn-success rounded-2" style={buttonStyle}   onClick={toggleAddNew}><i className="fa fa-plus"></i> Add New</button></div>
            </div>
        </header>
        <Modal isOpen={modalAddNew} toggle={toggleAddNew} backdrop={false} size="lg" {...args}>
        <ModalHeader toggle={toggleAddNew} className="border-0 text-bg-success" close={<></>}>Add Collection</ModalHeader>
        <ModalBody>
          <Editor artistName="" albumName="" price={0.00} releaseDate="" imageUrl="" sampleUrl="" onChange={onChange}/>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={onSimpan}>
            Save
          </Button>{' '}
          <Button color="secondary" onClick={toggleAddNew}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>


       
        {args.children}
    </>
  )
}
