import { useState,useEffect } from 'react';
import axios from 'axios';
import CONFIG from './core/config';
import Header from './components/header';
import Datagrid from './components/datagrid';

function App() {
  //var [searchText, setSearchText] = useState('');
  var [data, setData] = useState({list:[], error:true});
  const onSearch = (search) => {
    //setSearchText(search);
    makeAPICall(search)
  }

  const makeAPICall = async (query) => {
    try {

      const response = await axios.get(`${CONFIG.API_ENDPOINT}media${query?`?query=${query}`:''}`);
      const datax = await response.data;
      setData({list:datax, error:false});
      
    }
    catch (e) {
      
    }
  }

  useEffect(() => {makeAPICall()},[]);

  const onUpdate = async (e) => {
    if ((e.albumName === undefined)||(e.albumName.trim() === '') || (e.artistName === undefined)||(e.artistName.trim() === '')  || (e.imageUrl === undefined)||(e.imageUrl.trim() === '')  || (e.sampleUrl === undefined)||(e.sampleUrl.trim() === '')   || (e.releaseDate === undefined)||(e.releaseDate.trim() === '') )
      return false;
    await axios({
        url:`${CONFIG.API_ENDPOINT}media/${e.artistId}`,
        method:'put',
        data:e,
    }); 
    
    makeAPICall();
  return true;

  }

  const onDelete = async (e) => {
    if ((e.albumName === undefined)||(e.albumName.trim() === '') || (e.artistName === undefined)||(e.artistName.trim() === '')  || (e.imageUrl === undefined)||(e.imageUrl.trim() === '')  || (e.sampleUrl === undefined)||(e.sampleUrl.trim() === '')   || (e.releaseDate === undefined)||(e.releaseDate.trim() === '') )
      return false;
    await  axios.delete(`${CONFIG.API_ENDPOINT}media/${e.artistId}`);
    makeAPICall();
    return true;

  }

  const onSave = async (e) => {
    console.log(e);
    if ((e.albumName === undefined)||(e.albumName.trim() === '') || (e.artistName === undefined)||(e.artistName.trim() === '')  || (e.imageUrl === undefined)||(e.imageUrl.trim() === '')  || (e.sampleUrl === undefined)||(e.sampleUrl.trim() === '')   || (e.releaseDate === undefined)||(e.releaseDate.trim() === '') )
      return false;
      e.artistId = isNaN(parseInt(e.artistId))?0:parseInt(e.artistId)
      e.price = isNaN(parseInt(e.price))?0:parseInt(e.price)
      await axios({
        url:`${CONFIG.API_ENDPOINT}media`,
        method:'post',
        data:e,
      }); 
      makeAPICall();
    return true;
  }
  
  return (
    <div className="App">
      <Header className="App-header" onSave={onSave}  onSearch={onSearch}>
      </Header>
      <div className="body-grid" >
           <Datagrid data={data} onUpdate={onUpdate} onDelete={onDelete}/>
        </div>
    </div>
  );
}

export default App;
